

.skills-card {
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: rgb(207, 207, 226);
  margin: 20px auto;
  text-align: center;
  max-width: 500px;
  width: 80%;
  border: 1px solid black;
}

.skills-card h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 10px;
}

.skills-row {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  grid-column-gap: 50px;
  grid-row-gap: 50px;
  justify-content: center;
}

.skill-item {
  background-color: white;
  padding: 10px;
  border-radius: 15px;
  transition: 0.3s ease-in-out;
  box-shadow: 0 4px 8px rgba(134, 151, 168, 0.1);
  border: 1px solid #ddd;
}

.skill-item:hover {
  box-shadow: 0 8px 16px rgba(134, 151, 168, 0.2);
}

.skill-item img {
  width: 35px;
  height: 35px;
}

.skill-item p {
  margin-top: 10px;
  font-size: 0.7rem;
  color: #555;
}


/* Adjusts skills card for smaller screens */
@media only screen and (max-width: 768px) {
  .skills-card {
    width: 80%; /* Adjusted width to match other components */
    padding: 15px; /* Adjusted padding */
    border-radius: 10px; /* Decrease border radius */
    margin: 10px auto 0px 40px; /* Centered margin */
  }

  .skills-row {
    grid-template-columns: repeat(auto-fill, minmax(60px, 1fr)); /* Smaller columns for smaller screens */
    grid-column-gap: 20px; /* Adjusted gap between columns */
    grid-row-gap: 30px; /* Adjusted gap between rows */
  }

  .skill-item {
    padding: 8px; /* Adjusted padding */
  }
}

@media only screen and (min-width: 769px) and (max-width: 1200px) {
  .skills-card {
      width: 70%; /* Adjusted width for iPad and Nest Hub */
      padding: 15px; /* Adjusted padding */
      border-radius: 10px; /* Keep border radius smaller */
      margin: 20px auto; /* Centered margin */
  }
  
  .skills-row {
      grid-template-columns: repeat(auto-fill, minmax(60px, 1fr)); /* Grid adjustments */
      grid-column-gap: 30px; /* Adjusted gap between columns */
      grid-row-gap: 30px;   /* Adjusted gap between rows */
  }
}



